<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <a class="navbar-brand" href="@controllers.routes.ApplicationController.index">Tic Tac Toe 4D</a>

    <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
      <div class="navbar-nav">
        <a class="nav-item nav-link" href="@controllers.routes.ApplicationController.vueIndex()">Play</a>
        <a class="nav-item nav-link" href="@controllers.routes.ApplicationController.index">@messages("home")</a>
        <a class="nav-item nav-link" href="https://github.com/sara-ui/de.htwg.wt.TicTacToe4D">GitHub</a>
      </div>
    </div>

    <div class="collapse navbar-collapse navbar-right" id="navbarNavAltMarkup2">
      <div class="navbar-nav">
        @user.map { u =>
        <a class="nav-link" href="@controllers.routes.ApplicationController.index">@u.name</a>
        @if(u.loginInfo.providerID == com.mohiva.play.silhouette.impl.providers.CredentialsProvider.ID) {
        <a class="nav-item nav-link" href="@controllers.routes.ChangePasswordController.view">@messages("change.password")</a>
        }
        <a class="nav-item nav-link" href="@controllers.routes.ApplicationController.signOut">@messages("sign.out")</a>
        }.getOrElse {
        <a class="nav-item nav-link" href="@controllers.routes.SignInController.view">@messages("sign.in")</a>
        <a class="nav-item nav-link" href="@controllers.routes.SignUpController.view">@messages("sign.up")</a>
        }
      </div>
    </div>

  </nav>
  <div class="row justify-content-center w-100 m-0 h-100">
    <div class="col-lg-8 col-md-12 d-flex">
      <div class="m-auto w-100" style="width: 100%;max-width: 420px;">
        <div class="d-flex flex-column">
          <div class="text-center mb-4">
            <img
              class="img-fluid mb-4"
              src="/frontend/tictactoe-vue/public/img/TicTacToe4D_Transparent.png"
              alt="TicTacToe4D"
            />
            <h2 class="h3 mb-3 font-weight-normal">TicTacToe 4D</h2>
          </div>
          <login-form :payload="payload" @submit="submit"></login-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import LoginForm from "../components/LoginForm";
export default {
  name: "Login",
  components: { LoginForm },
  data: () => ({
    payload: {
      player1: "",
      player2: ""
    }
  }),
  methods: {
    submit() {
      this.login(this.payload);
      this.$router.push({ name: "Game" });
    },
    ...mapActions(["login"])
  }
};
</script>

